import React, { useState, useEffect } from 'react'
import { Box, TextField, Paper, Button, CircularProgress, Typography, Dialog as MDialog, DialogTitle, DialogContent, DialogContentText, DialogActions, Checkbox } from '@material-ui/core'
import MaterialTable from 'material-table'
import { format } from 'date-fns'
import producer from 'immer'

import '../components/queryTrace/queryTrace.scss'
import API from '../api/Api'

import DefaultTemplate from '../components/template/DefaultTemplate'
import StartEndDatePicker from '../components/datePicker/StartEndDatePicker'
import Dialog from '../components/dialog/Dialog'
import Table from '../components/table/Table'

const delay = 150
let timeoutHandle

const DISP_CLASS = {
  0: '',
  1: '',
  2: 'MMS',
  4: '',
  6: '',
  8: 'EMAIL',
  'auto': 'AUTO'
}

const DISP_STATUS = {
  waiting: '',
  ing: '',
  done: ''
}

const DM_TYPE = {
  registered: '',
  general: ''
}

const queryTraceInfoColumns = [
  {
    field: 'regino',
    title: ''
  },
  {
    title: '/',
    render: rowData => (
      <>
        {rowData.sendnm}
        <br />
        {rowData.itemlist[0].sortingdate}
      </>
    )
  },
  {
    title: '/',
    render: rowData => (
      <>
        {rowData.recevnm}
        <br />
        {rowData.eventymd}
      </>
    )
  },
  {
    field: 'mailkindnm',
    title: ''
  },
  {
    title: '',
    render: rowData => (
      <>
        {rowData.itemlist[rowData.itemlist.length - 1].tracestatus}
        {rowData.itemlist[rowData.itemlist.length - 1].nondelivreasnnm && (
          <>
            <br />
            : <span style={{ color: '#ef5350' }}>{rowData.itemlist[rowData.itemlist.length - 1].nondelivreasnnm}</span>
          </>
        )}
      </>
    )
  },
]

const queryTraceColumns = [
  {
    field: 'sortingdate',
    title: ''
  },
  {
    field: 'eventhms',
    title: ''
  },
  {
    field: 'eventregiponm',
    title: ''
  },
  {
    field: 'tracestatus',
    title: '',
    render: rowData => (
      <>
        {rowData.tracestatus}
        {rowData.nondelivreasnnm && (
          <>
            <br/>
            : <span style={{ color: '#ef5350' }}>{rowData.nondelivreasnnm}</span>
          </>
        )}
      </>
    )
  }
]


const PostingLookup = () => {
  const signUserType = localStorage.getItem('type')

  const tableRef = React.useRef()

  const [popup, setPopup] = useState(false)

  const [agencyData, setAgencyData] = useState([])

  const [dialog, setDialog] = useState({
    open: false,
    query: '',
    data: false
  })

  const [filter, setFilter] = useState({
    org_code: '',
    dept_code: '',
    start: new Date('2019-01-01'),
    end: new Date()
  })

  const [pageData, setPageData] = useState({
    page: 0,
    nowBlock: 0,
    totalCount: 0,
    html: [],
    pageSize: 100
  })

  const columns = [
    {
      field: 'org_name',
      title: ''
    },
    {
      field: 'dept_name',
      title: ''
    },
    {
      field: 'reg_dt',
      title: '',
      render: rowData => format(new Date(rowData.reg_dt), 'yyyy-MM-dd HH:mm:dd')
    },
    {
      field: 'name',
      title: ''
    },
    {
      field: 'template_code',
      title: ' '
    },
    {
      field: 'doc_title',
      title: ' '
    },
    {
      field: 'dm_type',
      title: ' '
    },
    {
      field: 'dm_reg_num',
      title: '',
      render: rowData => (
        <Button color="primary" onClick={() => handleOpenDialog(rowData.dm_reg_num)}>{rowData.dm_reg_num}</Button>
      )
    },
    {
      field: 'disp_class',
      title: ''
    },
    {
      field: 'disp_status',
      title: ''
    },
    {
      field: 'request_id',
      title: ''
    },
    {
      field: 'groupId',
      title: ' '
    },
    {
      field: 'is_readed',
      title: ''
    },
    {
      field: 'read_at',
      title: '',
      render: rowData => rowData.read_at ? format(new Date(rowData.read_at), 'yyyy-MM-dd HH:mm:dd') : ''
    }
  ]

  const init = async () => {
    if (signUserType === 'org') {
      const userId = localStorage.getItem('userId')
      const [user] = await API.getAccount('org', userId)
      const { org_code, dept_code } = await API.searchOrg('name', user.org_name, user.dept_name)

      setFilter({
        ...filter,
        org_code,
        dept_code
      })
    } else {
      const response = await API.getOrgs()

      setAgencyData(response)
    }
  }

  const getPostingLookup = async(page, page_size) => {
    const { hits } = await API.getPostSend({
      ...filter,
      page_from: page * page_size,
      page_size
    })

    if (!hits || hits.hits.length < 1) {
      return {
        data: [],
        page: page,
        totalCount: 0
      }
    }

    const { org_name, dept_name } = await API.searchOrg('code', filter.org_code, filter.dept_code)

    const data = hits.hits.map(item => ({
      org_name,
      dept_name,
      name: item._source.binding.reserved.additional.receiver.name,
      dm_type: item._source.binding.reserved.essential.dispatching.dm ? (DM_TYPE[item._source.binding.reserved.essential.dispatching.dm['type']] || '') : '',
      dm_reg_num: item._source.binding.reserved.essential.dispatching.dm ? item._source.binding.reserved.essential.dispatching.dm['reg-num'] : '',
      reg_dt: item._source.status.time['@analysised'],
      read_at: item._source.status.time['@read'],
      template_code: item._source.binding.reserved.essential.template.code,
      doc_title: item._source.binding.reserved.essential.template.title,
      disp_class: DISP_CLASS[item._source.binding.reserved.essential.dispatching.class],
      disp_status: DISP_STATUS[item._source.status.dispatching],
      request_id: item._source.binding.reserved.essential.search['request-id'],
      groupId: item._source.binding.reserved.essential.search['group-by'],
      is_readed: String(item._source.status.read) === '0' ? '-' : ''
    })).map(v => ({
      ...v,
      reg_dt: format(new Date(v.reg_dt), 'yyyy-MM-dd HH:mm:dd'),
      dm_reg_num: v.dm_reg_num+''
    }))

    setPageData(producer(v => {
      v.totalCount = hits.total
      v.page = page
      v.pageSize = page_size
    }))

    return {
      data,
      page: page,
      totalCount: hits.total
    }
  }

  const handleChangeFilterValue = (key, value) => {
    setFilter(producer(draft => {
      draft[key] = value
    }))
  }

  const handleChangeDate = (start, end) => {
    setFilter({
      ...filter,
      start,
      end
    })
  }

  const queryChange = () => {
    clearTimeout(timeoutHandle)
  
    timeoutHandle = setTimeout(() => {
      tableRef.current && tableRef.current.onQueryChange()
    }, delay)
  }

  const handleOpenDialog = (query) => {
    setDialog({
      ...dialog,
      open: true,
      data: false,
      query
    })
  }

  const handleCloseDialog = () => {
    setDialog({
      ...dialog,
      open: false
    })
  }

  const queryTrace = async () => {
    const { trace } = await API.queryTrace(dialog.query)
    let data = null

    if (trace) {
      data = Object.keys(trace).reduce((acc, key) => ({
        ...acc,
        [key]: trace[key][0]
      }), {})

      data.itemlist = data.itemlist.item.map(item => Object.keys(item).reduce((acc, key) => ({
        ...acc,
        [key]: item[key][0]
      }), {}))
    }

    setDialog({
      ...dialog,
      data
    })
  }

  const getPageBlock = () => {
    const page = pageData.page
    const totalCount = Math.ceil(pageData.totalCount / pageData.pageSize)
    const nowBlock = Math.floor(page / 10)

    let html = []

    for (let i = nowBlock*10+1; i <= (nowBlock+1)*10; i++) {
      if (i <= totalCount) {
        html.push(<button className={i-1 === page ? 'active' : ''} onClick={() => {
          tableRef.current.state.query.page = i - 1
          tableRef.current.state.pageSize = pageData.pageSize
          tableRef.current.onQueryChange()
        }}>{i}</button>)
      }
    }

    setPageData(producer(v => {
      v.nowBlock = nowBlock
      v.html = html
    }))
  }

  useEffect(() => {
    getPageBlock()
  }, [pageData.page, pageData.totalCount, pageData.pageSize])

  useEffect(() => {
    init()
  }, [])

  useEffect(() => {
    queryChange()
  }, [filter])

  useEffect(() => {
    if (dialog.open) {
      setTimeout(() => {
        queryTrace()
      }, 1000)
    }
  }, [dialog.open])


  return (
    <DefaultTemplate>

      <Dialog open={dialog.open} onClose={handleCloseDialog} message={{ title: ' ' }} maxWidth="md" className="queryTraceDialog" fullWidth>
        {dialog.data === false && (
          <div className="empty">
            <CircularProgress />
          </div>
        )}

        {dialog.data === null && (
          <div className="empty">
            <Typography>  </Typography>
          </div>
        )}

        {(dialog.data) instanceof Object && (
          <>
            <Table
              title=" "
              columns={queryTraceInfoColumns}
              data={[ dialog.data ]}
              options={{
                paging: false,
                columnsButton: false
              }}
            />

            <br />
            <br />

            <Table
              title=" "
              columns={queryTraceColumns}
              data={dialog.data.itemlist}
              options={{
                paging: false,
                columnsButton: false
              }}
            />
          </>
        )}
      </Dialog>

      <Box marginY={2}>
        <Paper>
          <Box display="flex" alignItems="center" justifyContent="space-between" padding={2}>
            <Box display="flex" alignItems="center">
              {signUserType === 'system' && (
                <Button color="primary" variant="contained" onClick={() => setPopup(true)}> </Button>
              )}
            </Box>

            <StartEndDatePicker
              start={filter.start}
              end={filter.end}
              onChange={handleChangeDate}
            />
          </Box>
        </Paper>
      </Box>

      <MaterialTable
        title="  "
        tableRef={tableRef}
        columns={columns}
        data={query =>
          new Promise((res, rej) => {
            getPostingLookup(query.page, query.pageSize)
              .then(result => res(result))
          })
        }
        options={{
          draggable: false,
          search: false,
          doubleHorizontalScroll: true,
          columnsButton: true,
          filtering: false,
          exportButton: true,
          pageSize: pageData.pageSize,
          pageSizeOptions: [100, 50, 20],
        }}
        components={{
          Container: props => (
            <Paper {...props} className="custom_table scrollable" />
          )
        }}
        localization={{
          body: {
            emptyDataSourceMessage: '  .',
            filterRow: {
              filterTooltip: ''
            },
            editRow: {
              saveTooltip: '',
              cancelTooltip: '',
              deleteText: '   ?'
            },
            addTooltip: '',
            deleteTooltip: '',
            editTooltip: ''
          },
          grouping: {
            placeholder: '   '
          },
          toolbar: {
            searchTooltip: '',
            searchPlaceholder: '',
            exportTitle: ' ',
            exportName: 'CSV ',
            showColumnsTitle: '',
            addRemoveColumns: '   '
          },
          pagination: {
            labelRowsSelect: ``,
            firstTooltip: ' ',
            previousTooltip: ' ',
            nextTooltip: ' ',
            lastTooltip: ' ',
            labelDisplayedRows: `{count}  {from}~{to}`
          }
        }}
      />

      <div className="paging">
        {pageData.html}
      </div>

      <MDialog open={popup} onClose={() => setPopup(false)} >
        <DialogTitle></DialogTitle>

        <DialogContent>
          <DialogContentText>
              .     .
          </DialogContentText>

          <table className="c-table">
            <thead>
              <th> </th>
              <th> </th>
              <th> </th>
              <th> </th>
              <th></th>
            </thead>

            <tbody>
              {agencyData.map(data => {

                return (
                  <tr key={data.idx}>
                    <td>{data.org_code}</td>
                    <td>{data.dept_code}</td>
                    <td>{data.org_name}</td>
                    <td>{data.dept_name}</td>
                    <td>
                      <Checkbox
                        color="primary"
                        name="aaaa"
                        checked={filter.org_code === data.org_code && filter.dept_code === data.dept_code}
                        onChange={e => {
                          if (e.target.checked) {
                            setFilter({
                              ...filter,
                              org_code: data.org_code,
                              dept_code: data.dept_code
                            })
                          }
                        }}
                      />
                    </td>
                  </tr>
                )
              })}
            </tbody>
          </table>
        </DialogContent>

        <DialogActions>
          <Button color="primary" onClick={() => setPopup(false)}>
            
          </Button>
        </DialogActions>
      </MDialog>
    </DefaultTemplate>
  )
}

export default PostingLookup